import React from "react"; // Importing React in Navbar Component
import '../CompCSS/Navbar.css'; // Importing Css Of Navbar
import { useState } from "react";

function Navbar()
{

    const [navbaract, setnavbaract] = useState(false); // state variable for navbar active and passive options

    const changeBg = () => {  //function to change navbar from passive to active 
        if(window.scrollY >= 10){
            setnavbaract(true);
        } else{
            setnavbaract(false);
        }
    };

    window.addEventListener('scroll', changeBg); // function to detect scroll

    return(
        <section className={navbaract ? 'NavbarMain active' : 'NavbarMain passive'} >
            
            <div className="NavbarContainer">
                
                <input type="checkbox" name="" id="hamcheck"></input>

                <div className="NavbarLogo">
                    <h3 className="NavLogo"> Code <span> Hustlers</span> </h3>
                </div>

                <div className="NavbarButton">
                    <div className="NavbarLinks">
                        <ul >
                            <li className="nav-link" style={{"--i": '.6s'}}>
                                <a href="#"> Home </a>
                            </li>
                            <li className="nav-link" style={{"--i": '.85s'}}>
                                <a href="#"> Course </a>
                            </li>
                            <li className="nav-link" style={{"--i": '1.1s'}}>
                                <a href="#"> Contact Us </a>
                            </li>
                            <li className="nav-link" style={{"--i": '1.35s'}}>
                                <a href="#"> Feedback </a>
                            </li>
                        </ul>
                    </div>

                    <div className="NavbarLogSign" style={{"--i": '1.8s'}}>
                        <a href="#" className="navbtn transparent"> Log in </a>
                        <a href="#" className="navbtn solid"> Sign up</a>
                    </div>
                </div>
                
                <div className="hamburger-menu-container">
                    <div className="hamburger-menu">
                        <div></div>
                    </div>
                </div>

            </div>
        
        </section>
    );
}

export default Navbar;